import React, { Component } from 'react';

const Breadcrumb = () => (
	<div className="breadcrumb">
		<ul>
			<li>
				<a href="#/">正丰阀门集团</a>
			</li>
			<li>
				<span>&gt;</span>
				<a href="#/">企业新闻</a> 
				<span>&gt;</span>
			</li>
		</ul>
	</div>
);

export default Breadcrumb;


